@import url("https://fonts.googleapis.com/css2?family=Inter:wght@300;400;500;700&display=swap");
html {
  box-sizing: border-box;
  font-size: 100%;
  line-height: 1.15;
  scroll-behavior: smooth;
  width: 100%;
  height: 100%;
}
body {
  font-family: "Inter", Arial, Helvetica, sans-serif;
  font-weight: 400;
  font-style: normal;
  text-decoration: none;
  line-height: 1.15;
  color: #000000;
  background-color: rgb(237, 241, 255);
}
* {
  margin: 0;
  padding: 0;
  box-sizing: inherit;
}
button,
select {
  text-transform: none;
  border: none;
  cursor: pointer;
}
a {
  color: inherit;
  text-decoration: none;
}
input {
  padding: 2px 4px;
}
img {
  display: block;
}
.cangra-main {
  position: relative;
}
.cangra-nav {
  flex-direction: row;
  align-items: center;
  justify-content: space-between;
  display: flex;
  position: fixed;
  width: 100%;
  padding: 16px 64px;
  z-index: 1;
}
.cangra-nav .logo {
  position: relative;
  width: 110px;
  height: 47px;
  object-fit: cover;
}
.cangra-nav.scrolled {
  background-color: rgb(41, 64, 145);
  transition: all 0.3s linear;
}
.cangra-nav__items {
  flex-direction: row;
  align-items: center;
  justify-content: center;
  display: flex;
  gap: 1rem;
  position: relative;
}
.cangra-nav__item {
  color: white;
  font-size: 14px;
}
.cangra-nav__button {
  flex-direction: row;
  align-items: center;
  justify-content: center;
  display: flex;
  position: relative;
  background-color: rgb(255, 215, 0);
  border-radius: 8px;
  color: rgb(52, 80, 181);
  font-size: 14px;
  font-weight: 500;
  padding: 10px 20px;
}
.cangra-hero {
  flex-direction: row;
  align-items: center;
  justify-content: center;
  display: flex;
  position: relative;
  width: 100vw;
  height: 100vh;
  background: linear-gradient(to top, hsl(226.81, 100%, 64.31%), hsl(226.81, 98.15%, 63.88%) 15.62%, hsl(226.81, 93.2%, 62.68%) 29.98%, hsl(226.81, 86.13%, 60.81%) 43.05%, hsl(226.8, 77.91%, 58.38%) 54.81%, hsl(226.8, 69.3%, 55.5%) 65.26%, hsl(226.79, 60.9%, 52.28%) 74.38%, hsl(226.78, 55.64%, 48.84%) 82.13%, hsl(226.77, 55.69%, 45.36%) 88.52%, hsl(226.76, 55.76%, 42.05%) 93.52%, hsl(226.75, 55.83%, 39.22%) 97.11%, hsl(226.74, 55.89%, 37.22%) 99.27%, hsl(226.73, 55.91%, 36.47%));
}
@media only screen and (max-width: 1200px) {
  .cangra-hero {
    background: black;
  }
}
.cangra-hero__wrapper {
  flex-direction: column;
  align-items: flex-start;
  justify-content: center;
  display: flex;
  position: relative;
}
.cangra-hero__wrapper .title {
  width: 600px;
  color: white;
  font-size: 2.5rem;
  font-weight: 700;
  margin-bottom: 1rem;
}
@media only screen and (max-width: 1200px) {
  .cangra-hero__wrapper .title {
    width: 500px;
    font-size: 2rem;
    margin-bottom: 0.5rem;
  }
}
.cangra-hero__wrapper .desc {
  width: 540px;
  height: auto;
  color: white;
  font-size: 1.25rem;
  line-height: 2.5rem;
  margin-bottom: 1rem;
}
@media only screen and (max-width: 1200px) {
  .cangra-hero__wrapper .desc {
    width: 490px;
    font-size: 1rem;
    line-height: 2rem;
  }
}
.cangra-hero__wrapper .cta {
  flex-direction: row;
  align-items: center;
  justify-content: center;
  display: flex;
  background-color: rgb(255, 215, 0);
  border-radius: 10px;
  color: rgb(52, 80, 181);
  font-weight: 500;
  padding: 16px 32px;
}
@media only screen and (max-width: 1200px) {
  .cangra-hero__wrapper .cta {
    font-size: 14px;
  }
}
.cangra-hero__img {
  max-width: 100%;
  height: 370px;
  object-fit: cover;
}
@media only screen and (max-width: 1200px) {
  .cangra-hero__img {
    height: 280px;
  }
}
.cangra-boxes {
  flex-direction: row;
  align-items: center;
  justify-content: center;
  display: flex;
  gap: 1rem;
  position: relative;
  bottom: 130px;
  width: 100%;
}
.cangra-boxes .cangra-box {
  flex-direction: column;
  align-items: center;
  justify-content: center;
  display: flex;
  position: relative;
  background-color: white;
  border-radius: 10px;
  box-shadow: 0px 10px 15px -3px rgba(73, 113, 255, 0.1), 0px 4px 6px -4px rgba(73, 113, 255, 0.1);
  padding: 24px 16px;
}
@media only screen and (max-width: 1200px) {
  .cangra-boxes .cangra-box {
    padding: 20px 10px;
  }
}
.cangra-boxes .cangra-box__icon {
  flex-direction: row;
  align-items: center;
  justify-content: center;
  display: flex;
  width: 80px;
  height: 80px;
  margin-bottom: 1rem;
  overflow: hidden;
}
@media only screen and (max-width: 1200px) {
  .cangra-boxes .cangra-box__icon {
    width: 75px;
    height: 75px;
  }
}
.cangra-boxes .cangra-box__icon-bg {
  flex-direction: column;
  align-items: center;
  justify-content: center;
  display: flex;
  position: relative;
  width: 100%;
  height: 100%;
  background-color: rgb(255, 251, 230);
  border-radius: 10px;
}
@media only screen and (max-width: 1200px) {
  .cangra-boxes .cangra-box__icon-bg {
    width: 75px;
    height: 75px;
  }
}
.cangra-boxes .cangra-box__title {
  font-size: 16px;
  margin-bottom: 1rem;
}
.cangra-boxes .cangra-box__desc {
  width: 326px;
  color: rgb(68, 68, 68);
  font-size: 12px;
  line-height: 30px;
  text-align: center;
}
.cangra-clients {
  flex-direction: column;
  align-items: center;
  justify-content: center;
  display: flex;
  position: relative;
  border-color: transparent;
  margin: 2rem 0 7rem 0;
}
.cangra-clients__title {
  color: rgb(52, 80, 181);
  font-size: 2rem;
  font-weight: 700;
  margin-bottom: 40px;
}
.cangra-clients__cards {
  flex-direction: "";
  align-items: center;
  justify-content: "";
  display: flex;
  gap: 1rem;
  position: relative;
  box-sizing: border-box;
}
.cangra-clients__cards .card {
  flex-direction: column;
  align-items: center;
  justify-content: center;
  display: flex;
  position: relative;
  background-color: white;
  border-radius: 10px;
  box-shadow: 0px 10px 15px -3px rgba(73, 113, 255, 0.1), 0px 4px 6px -4px rgba(73, 113, 255, 0.1);
  padding: 24px 16px;
}
.cangra-clients__cards .card img {
  position: relative;
  width: 64px;
  height: 64px;
  border-color: transparent;
  border-radius: 100px;
  box-sizing: border-box;
  margin-bottom: 1rem;
  object-fit: cover;
}
.cangra-clients__cards .card__profile {
  flex-direction: column;
  align-items: center;
  justify-content: flex-start;
  display: flex;
  position: relative;
  flex-shrink: 1;
  margin-bottom: 0.5rem;
}
.cangra-clients__cards .card__profile .name {
  font-size: 1rem;
  font-weight: 700;
  margin-bottom: 0.5rem;
}
.cangra-clients__cards .card__profile .company {
  font-size: 14px;
}
.cangra-clients__cards .card__desc {
  width: 326px;
  font-size: 12px;
  font-stretch: normal;
  line-height: 2rem;
  text-align: center;
  text-decoration: none;
}
.cangra-customers {
  flex-direction: column;
  align-items: center;
  justify-content: center;
  display: flex;
  position: relative;
  width: 100%;
  height: 20rem;
  background-image: linear-gradient(135deg, rgb(73, 113, 255) 0%, rgb(65, 101, 229) 34%, rgb(56, 87, 198) 67%, rgb(41, 64, 145) 100%);
}
.cangra-customers__title {
  color: white;
  font-size: 2rem;
  font-weight: 700;
  margin-bottom: 2rem;
}
.cangra-customers__wrapper {
  flex-direction: "";
  align-items: flex-start;
  justify-content: "";
  display: flex;
  gap: 2rem;
  position: relative;
}
.cangra-customers__wrapper .company {
  flex-direction: row;
  align-items: center;
  justify-content: center;
  display: flex;
  width: 10rem;
  height: 7rem;
  background-color: white;
  border-radius: 10px;
  box-shadow: 0px 10px 15px -3px rgba(73, 113, 255, 0.1), 0px 4px 6px -4px rgba(73, 113, 255, 0.1);
  padding: 12px 48px;
}
.cangra-customers__wrapper .company__wrapper {
  flex-direction: column;
  align-items: center;
  justify-content: center;
  display: flex;
  width: 64px;
}
.cangra-customers__wrapper .company__wrapper .icon {
  flex-direction: row;
  align-items: center;
  justify-content: center;
  display: flex;
  position: relative;
  width: 40px;
  height: 40px;
  margin-bottom: 10px;
}
.cangra-customers__wrapper .company__wrapper .icon img {
  position: relative;
}
.cangra-customers__wrapper .company__wrapper .name {
  width: 64px;
  color: rgb(166, 166, 166);
  font-size: 12px;
  font-weight: 500;
  line-height: 15px;
  text-align: center;
}
.cangra-features {
  flex-direction: column;
  align-items: center;
  justify-content: center;
  display: flex;
  width: 100%;
  margin: 80px 0;
}
.cangra-features__title {
  color: rgb(52, 80, 181);
  font-size: 2.5rem;
  font-weight: 700;
  margin-bottom: 40px;
}
.cangra-features img {
  width: 965px;
  height: 550px;
  border-radius: 20px;
  margin-bottom: 40px;
  object-fit: cover;
}
.cangra-features .cangra-feature {
  flex-direction: row;
  align-items: center;
  justify-content: center;
  display: flex;
  position: relative;
}
.cangra-features .cangra-feature .feature-col {
  flex-direction: column;
  align-items: flex-start;
  justify-content: flex-start;
  display: flex;
  gap: 24px;
  position: relative;
  margin-right: 4rem;
}
.cangra-features .cangra-feature .feature-col .feature {
  flex-direction: row;
  align-items: center;
  justify-content: center;
  display: flex;
  position: relative;
}
.cangra-features .cangra-feature .feature-col .feature img {
  position: relative;
  width: 32px;
  height: 32px;
  border-color: transparent;
  box-sizing: border-box;
  margin: 0 10px 0 0;
  object-fit: cover;
}
.cangra-features .cangra-feature .feature-col .feature-name {
  font-size: 1.5rem;
  font-weight: 600;
}
.cangra-packages {
  flex-direction: row;
  align-items: flex-start;
  justify-content: flex-start;
  display: flex;
  position: relative;
  width: 100%;
  background-image: linear-gradient(0deg, rgb(73, 113, 255) 0%, rgb(65, 101, 229) 34%, rgb(56, 87, 198) 67%, rgb(41, 64, 145) 100%);
}
.cangra-packages img {
  position: absolute;
  width: 100%;
  height: 100%;
  object-fit: cover;
}
.cangra-packages__wrapper {
  flex-direction: row;
  align-items: center;
  justify-content: center;
  display: flex;
  width: 100%;
  padding: 4rem;
}
.cangra-packages__wrapper .header {
  flex-direction: column;
  align-items: stretch;
  justify-content: flex-start;
  display: flex;
  position: relative;
  margin-right: 5rem;
}
.cangra-packages__wrapper .header__title {
  color: white;
  font-size: 2.5rem;
  font-weight: 700;
  margin-bottom: 0.5rem;
}
.cangra-packages__wrapper .header__desc {
  color: white;
  font-size: 1.5rem;
  font-weight: 400;
  opacity: 0.75;
}
.cangra-packages .card {
  flex-direction: column;
  align-items: stretch;
  justify-content: flex-start;
  display: flex;
  position: relative;
  border: 1px solid rgb(52, 80, 181);
  border-radius: 20px;
  box-shadow: 0px 10px 15px -3px rgba(73, 113, 255, 0.1), 0px 4px 6px -4px rgba(73, 113, 255, 0.1);
  overflow: hidden;
}
.cangra-packages .card__header {
  flex-direction: column;
  align-items: center;
  justify-content: center;
  display: flex;
  position: relative;
  background-color: rgb(52, 80, 181);
  padding: 24px 16px;
}
.cangra-packages .card__header .title {
  color: white;
  font-size: 1.5rem;
  font-weight: 600;
  margin-bottom: 1rem;
}
.cangra-packages .card__header .buttons {
  flex-direction: row;
  align-items: center;
  justify-content: center;
  display: flex;
  gap: 0.5rem;
  position: relative;
}
.cangra-packages .card__header .buttons .button {
  flex-direction: row;
  align-items: center;
  justify-content: center;
  display: flex;
  position: relative;
  background-color: transparent;
  border: 1px solid white;
  border-radius: 5px;
  color: white;
  cursor: pointer;
  font-size: 14px;
  padding: 8px 16px;
}
.cangra-packages .card__header .buttons .button.active {
  background-color: white;
  color: rgb(52, 80, 181);
}
.cangra-packages .card__desc {
  flex-direction: column;
  align-items: center;
  justify-content: center;
  display: flex;
  position: relative;
  width: 100%;
  background-color: white;
  padding: 24px 16px;
}
.cangra-packages .card__desc__wrapper {
  flex-direction: column;
  align-items: center;
  justify-content: flex-start;
  display: flex;
  gap: 0.5rem;
  position: relative;
  width: 100%;
  margin-bottom: 1rem;
}
.cangra-packages .card__desc__text {
  color: rgb(68, 68, 68);
  font-size: 1.25rem;
  line-height: 165%;
  text-align: center;
}
.cangra-packages .card .cangra-cangra-packages-card__desc__separator {
  width: 100%;
  height: 1px;
}
.cangra-packages .card__desc__price {
  color: rgb(52, 80, 181);
  font-size: 2rem;
  font-weight: 600;
  line-height: 165%;
}
.cangra-start {
  flex-direction: row;
  align-items: center;
  justify-content: space-between;
  display: flex;
  padding: 100px 320px;
  color: rgb(52, 80, 181);
}
.cangra-start__text {
  color: rgb(52, 80, 181);
  font-size: 3rem;
  font-weight: 700;
}
.cangra-start__cta {
  flex-direction: row;
  align-items: center;
  justify-content: center;
  display: flex;
  position: relative;
  background-color: rgb(255, 215, 0);
  font-size: 1.5rem;
  border-radius: 10px;
  padding: 16px 32px;
}
.cangra-footer {
  flex-direction: column;
  align-items: stretch;
  justify-content: flex-start;
  display: flex;
  gap: 2.5rem;
  position: relative;
  background-color: rgb(40, 62, 140);
  padding: 48px 0;
}
.cangra-footer__wrapper {
  flex-direction: column;
  align-items: flex-start;
  justify-content: flex-start;
  display: flex;
  gap: 2rem;
  position: relative;
  padding: 0 48px;
}
.cangra-footer__logo {
  position: relative;
  object-fit: cover;
}
.cangra-footer__desc {
  color: rgb(204, 204, 204);
  font-size: 14px;
}
.cangra-footer__links {
  flex-direction: row;
  align-items: stretch;
  justify-content: flex-start;
  display: flex;
  position: relative;
}
.cangra-footer__col {
  flex-direction: column;
  align-items: stretch;
  justify-content: flex-start;
  display: flex;
  gap: 0.5rem;
  position: relative;
  margin-right: 4rem;
}
.cangra-footer__link {
  color: rgb(204, 204, 204);
  font-size: 14px;
  line-height: 25px;
}
.cangra-footer__link.title {
  color: white;
  font-size: 18px;
  font-weight: 600;
  line-height: 25px;
}
.cangra-footer__separator {
  position: relative;
  width: 100%;
  height: 1px;
  border-top: 1px solid rgba(204, 204, 204, 0.5);
  content: " ";
}

